@import '../../../../../styles/templarios.abstracts';

:host {
  display: block;
  position: relative;
}

.product-search {
  &__item-wrapper {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    width: 100%;
  }

  &__image-container {
    flex: 0 0 auto;
    width: 48px;
    height: 48px;
    border-radius: 4px;
    margin-right: 16px;
    overflow: hidden;
  }

  &__image {
    object-fit: cover;
  }

  &__info {
    flex: 1 1 auto;
    display: flex;
    gap: 16px;
    justify-content: space-between;
    align-items: center;

    @include breakpoint-down('xs') {
      flex-direction: column;
      align-items: flex-start;
      gap: 4px;
    }
  }

  &__info-left {
    display: flex;
    flex-direction: column;
  }

  &__description {
    color: rgba(
      var(--tp-color-contrast-rgb, var(--tp-color-neutral-10-rgb)),
      0.7
    );

    @include breakpoint-down('sm') {
      display: none;
    }
  }

  &__info-right {
    display: flex;
    flex-direction: column;
    align-items: center;
    white-space: nowrap;

    @include breakpoint-down('xs') {
      flex-direction: row;
      gap: 8px;
    }
  }

  &__sale-price {
    text-decoration: line-through;
    color: rgba(
      var(--tp-color-contrast-rgb, var(--tp-color-neutral-10-rgb)),
      0.7
    );
  }

  &__loader-container {
    position: absolute;
    top: 9px;
    left: 9px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: var(--tp-color-base, var(--tp-color-neutral-2));
    width: 24px;
    height: 24px;
  }

  &__loader {
    box-sizing: border-box;
    width: 20px;
    height: 20px;
    border: 3px solid var(--tp-color-contrast, var(--tp-color-neutral-6));
    border-top-color: transparent;
    border-radius: 50%;
    animation: product-search-spinner 1.2s linear infinite;
  }

  &__search-page-link {
    width: 100%;
    text-align: center;
  }

  &__lightbox {
    position: absolute;
    top: 24px;
    right: 16px;
    display: flex;
    width: calc(100% - 96px);
    max-width: 240px;
    height: auto;
    box-shadow: -4px 4px 8px 1px rgba(var(--tp-color-neutral-1-rgb), 0.8);
    border-radius: 8px;
    overflow: hidden;
    z-index: 10000;
    opacity: 0;
    transition: 300ms opacity ease-in-out;

    &--show {
      opacity: 1;
    }
  }
}

:host(.product-search__searchbar-hover) .product-search__loader-container {
  background: var(--tp-color-neutral-3);
}

//
// animation
//
@keyframes product-search-spinner {
  100% {
    transform: rotate(360deg);
  }
}
