import { SearchbarCustomEvent } from '@ionic/core';
import { Component, Host, Listen, Prop, State, Watch, h } from '@stencil/core';
import { TpColor } from '../../../../../components';
import { ApiProduct } from '../../../../../utils/api/ecommerce/product.api';
import { Product } from '../../../../../utils/api/ecommerce/utils/interfaces/product.interface';
import { currency } from '../../../../../utils/filters/currency.filter';
import { createColorClasses } from '../../../../../utils/functions/color.function';
import { PRODUCT_SEARCH } from './utils/product-search.constant';

@Component({
  tag: 'tp-elem-product-search',
  styleUrl: 'product-search.scss',
  scoped: true,
})
export class TpElemProductSearch {
  /**
   * A variação de cor do componente.
   */
  @Prop({ reflect: true }) color?: TpColor;

  /**
   * O path da página de busca.
   */
  @Prop({ reflect: true }) searchPageUrl: string = '#';

  /**
   * A altura máxima do dropdown.
   */
  @Prop({ reflect: true }) maxDropdownHeight: number = 320;

  /**
   * O termo buscado pelo usuário.
   */
  @State() searchTerm: string = '';

  /**
   * Um conjunto de objetos com todos os dados dos produtos encontrados
   * com o termo 'searchTerm'.
   */
  @State() products: Product[] = [];

  /**
   * Configurações do lightbox.
   */
  @State() lightboxParams: { imageSrc: string; display: boolean } = {
    imageSrc: null,
    display: false,
  };

  /**
   * Se o usuário está ou não com o mouse sobre a caixa de busca.
   */
  @State() searchbarHovering = false;

  /**
   * Se o usuário está ou não com o mouse sobre a um item do dropdown.
   */
  @State() searchItemHovering = false;

  /**
   * Se o dropdown pode ou não ser mostrado.
   */
  @State() dropdownLocked = true;

  /**
   * Se uma busca está ou não sendo realizada.
   */
  @State() searching = false;

  /**
   * O abort controller da última chamada http feita pelo component.
   * Caso haja uma nova requisição e a anterior não tenha sido completada,
   * o abort controller permite que ela seja cancelada.
   */
  @State() currentAbortController: AbortController;

  /**
   * State e Prop watchers.
   */
  @Watch('searchTerm')
  async onUpdatedSearchTerm() {
    if (this.currentAbortController) this.currentAbortController.abort();

    if (!this.searchTerm) {
      this.products = [];
      if (this.searching) this.searching = false;
      return;
    }

    if (this.searchTerm.length < PRODUCT_SEARCH.LOWER_SKIP_SEARCH_LIMIT) {
      if (this.searching) this.searching = false;
      return;
    }

    this.currentAbortController = new AbortController();
    this.searching = true;
    this.dropdownLocked = !this.products.length;

    try {
      const { products } = await ApiProduct.getSearchedProducts(
        this.searchTerm,
        { signal: this.currentAbortController.signal },
      );

      if (this.searching) {
        this.products = products;
        this.searching = false;
        this.dropdownLocked = false;
      }
    } catch (error) {
      this.products = [];
      this.searching = false;
      this.dropdownLocked = false;
    }
  }

  /**
   * Event listeners.
   */
  @Listen('ionInput')
  onSearchInput(e: SearchbarCustomEvent) {
    const searchTerm = e.detail.value;

    this.searchTerm = searchTerm;
  }

  @Listen('ionFocus')
  onSearchFocus() {
    if (this.dropdownLocked && this.products.length) {
      this.dropdownLocked = false;
    }
  }

  @Listen('ionBlur')
  onSearchBlur() {
    if (this.searchItemHovering && this.products.length) return;

    if (!this.dropdownLocked) {
      this.dropdownLocked = true;
    }
  }

  /**
   * Métodos internos.
   */
  onMouseEnterImage(imageSrc: string) {
    this.lightboxParams = { imageSrc, display: true };
  }

  onMouseLeavesImage() {
    this.lightboxParams = { ...this.lightboxParams, display: false };
  }

  onMouseEnterSearchbar() {
    this.searchbarHovering = true;
  }

  onMouseLeavesSearchbar() {
    this.searchbarHovering = false;
  }

  onMouseEnterSearchItem() {
    this.searchItemHovering = true;
  }

  onMouseLeavesSearchItem() {
    this.searchItemHovering = false;
  }

  getDropdownItems() {
    const { color } = this;

    return this.products.length
      ? this.products.map(
          ({
            name,
            shortDescription,
            images,
            onSale,
            regularPrice,
            price,
            permalink,
          }) => (
            <ion-item
              href={permalink}
              lines="full"
              tp-spacingy="s08"
              color={color}
              onMouseEnter={() => this.onMouseEnterSearchItem()}
              onMouseLeave={() => this.onMouseLeavesSearchItem()}
            >
              <div class="product-search__item-wrapper">
                <div
                  onMouseEnter={() => this.onMouseEnterImage(images[0].src)}
                  onMouseLeave={() => this.onMouseLeavesImage()}
                  class="product-search__image-container"
                >
                  <img
                    class="product-search__image"
                    src={images[0].src}
                    alt=""
                  />
                </div>

                <div class="product-search__info">
                  <div class="product-search__info-left">
                    <ion-text tp-type="p16x">
                      <span>{name}</span>
                    </ion-text>

                    <ion-text
                      class="product-search__description"
                      tp-type="p14x"
                    >
                      <span>{shortDescription.replace(/<\/?p>/g, '')}</span>
                    </ion-text>
                  </div>

                  <div class="product-search__info-right">
                    {onSale && regularPrice && (
                      <ion-text
                        class="product-search__sale-price"
                        tp-type="p12x"
                      >
                        {currency(regularPrice, 'pt-br')}
                      </ion-text>
                    )}

                    <ion-text tp-type="p14x">
                      {currency(price, 'pt-br')}
                    </ion-text>
                  </div>
                </div>
              </div>
            </ion-item>
          ),
        )
      : [];
  }

  render() {
    const {
      color,
      searchPageUrl,
      maxDropdownHeight,
      products,
      searchTerm,
      dropdownLocked,
      searching,
      searchbarHovering,
    } = this;

    const dropdownItems = this.getDropdownItems();

    return (
      <Host
        class={createColorClasses(color, {
          'product-search': true,
          'product-search__searchbar-hover': !color && searchbarHovering,
        })}
      >
        <tp-autocomplete
          color={color}
          locked={dropdownLocked}
          style={{ '--tp-dropdown-max-height': `${maxDropdownHeight}px` }}
        >
          <tp-input-container class="product-search__searchbar" color={color}>
            <ion-searchbar
              inputMode="search"
              debounce={PRODUCT_SEARCH.DEBOUNCE_TIME}
              mode="md"
              search-icon="./../../../../assets/tp-search.svg"
              clear-icon="./../../../../assets/tp-x.svg"
              placeholder="Buscar produto"
              color={color}
              onMouseEnter={() => this.onMouseEnterSearchbar()}
              onMouseLeave={() => this.onMouseLeavesSearchbar()}
            ></ion-searchbar>
          </tp-input-container>

          {searching && (
            <div class="product-search__loader-container">
              <div class="product-search__loader"></div>
            </div>
          )}

          <tp-dropdown>
            {dropdownItems}

            {searchTerm && !products.length && (
              <ion-item lines="full" tp-spacingy="s08" color={color}>
                <ion-text tp-type="p14x">Nenhum produto encontrado</ion-text>
              </ion-item>
            )}

            {Boolean(products.length) && (
              <ion-item
                href={`${searchPageUrl}?query=${searchTerm}`}
                lines="full"
                tp-spacingy="s08"
                color={color}
                onMouseEnter={() => this.onMouseEnterSearchItem()}
                onMouseLeave={() => this.onMouseLeavesSearchItem()}
              >
                <ion-text
                  tp-type="p14x"
                  class="product-search__search-page-link"
                >
                  Ver todos os resultados
                </ion-text>
              </ion-item>
            )}
          </tp-dropdown>
        </tp-autocomplete>

        <div
          class={{
            'product-search__lightbox': true,
            'product-search__lightbox--show': this.lightboxParams.display,
          }}
        >
          <img src={this.lightboxParams.imageSrc} alt="" />
        </div>
      </Host>
    );
  }
}
